import { ComponentFixture, TestBed } from '@angular/core/testing';
import { RouterTestingModule } from '@angular/router/testing';
import { AdminDashboard } from './admin-dashboard';
import { HttpClientTestingModule } from '@angular/common/http/testing';

fdescribe('AdminDashboard', () => {
  let component: AdminDashboard;
  let fixture: ComponentFixture<AdminDashboard>;

  beforeEach(async () => {
    await TestBed.configureTestingModule({
      imports: [
        AdminDashboard, // âœ… Standalone component should be in imports
        RouterTestingModule, // âœ… For Router navigation
        HttpClientTestingModule // âœ… For HttpClient
      ]
    }).compileComponents();

    fixture = TestBed.createComponent(AdminDashboard);
    component = fixture.componentInstance;
    fixture.detectChanges();
  });

  it('should create', () => {
    expect(component).toBeTruthy();
  });

  it('should toggle menu', () => {
    expect(component.isMenuOpen).toBeFalse();
    component.toggleMenu();
    expect(component.isMenuOpen).toBeTrue();
  });

  it('should remove tokens and navigate on login', () => {
    spyOn(localStorage, 'removeItem');
    const routerSpy = spyOn(component['router'], 'navigate');

    component.onLogout();

    expect(localStorage.removeItem).toHaveBeenCalledWith('token');
    expect(localStorage.removeItem).toHaveBeenCalledWith('role');
    expect(localStorage.removeItem).toHaveBeenCalledWith('employeeId');
    expect(routerSpy).toHaveBeenCalledWith(['/']);
  });
});

